import React from "react";
import { useState, useEffect } from "react";
import Loading from "../components/Loading";
import { useParams, Link } from "react-router-dom";
import useFetch from "../hooks/useFetch";
import Error from "./Error";

const SingleCocktail = () => {
	const { id } = useParams();
	const { data, pending, error } = useFetch(
		`https://www.thecocktaildb.com/api/json/v1/1/lookup.php?i=${id}`
	);
	console.log(data && data[0]);

	if (pending && !error) {
		return <Loading />;
	} else if (!pending && !error) {
		return (
			<section className="section cocktail-section">
				<Link to={"/"} className="btn btn-primary">
					Back Home
				</Link>
				{data && (
					<h2 className="section-title">
						{data[0].strDrink && data[0].strDrink}
					</h2>
				)}
				{data && (
					<div className="drink">
						<img
							src={data[0].strDrinkThumb && data[0].strDrinkThumb}
							alt={data[0].strDrink && data[0].strDrink}
						/>
						<div className="drink-info">
							<p>
								<span className="drink-data">name : </span>
								{data[0].strDrink && data[0].strDrink}
							</p>
							<p>
								<span className="drink-data">category : </span>
								{data[0].strCategory && data[0].strCategory}
							</p>
							<p>
								<span className="drink-data">info : </span>
								{data[0].strAlcoholic && data[0].strAlcoholic}
							</p>
							<p>
								<span className="drink-data">glass : </span>
								{data[0].strGlass && data[0].strGlass}
							</p>
							<p>
								<span className="drink-data">
									instructions :{" "}
								</span>
								{data[0].strInstructions}
							</p>
							<p>
								<span className="drink-data">
									ingredients :
								</span>
								{Object.keys(data[0]).map((key) => {
									if (key.includes("strIngredient")) {
										return <span>{data[0][key]}</span>;
									}
								})}
							</p>
						</div>
					</div>
				)}
			</section>
		);
	} else {
		return <Error />;
	}
};

export default SingleCocktail;
